//ë°”ë€” ê°€ëŠ¥ì„±ì´ ìˆëŠ” ì£¼ì†Œ ë“±ì˜ ìš”ì†ŒëŠ” ë³€ìˆ˜ë¡œ ë§Œë“¤ì–´ì£¼ëŠ” ê²ƒì´ ì¢‹ë‹¤.
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

const store = {
    currentPage: 1,
};

function getData(url) {
    //get ë°©ì‹, api ì£¼ì†Œ, true=ë¹„ë™ê¸°ì ìœ¼ë¡œ, false=ë™ê¸°ì ìœ¼ë¡œ ê°’ì„ ê°€ì ¸ì˜¤ê² ë‹¤.
    ajax.open('GET', url, false);
    //send ëª…ë ¹ì–´ê°€ ìˆì–´ì•¼ ì‹¤ì œë¡œ ê°’ì„ ê°€ì ¸ì˜´. => ajax.response;
    ajax.send();
    return JSON.parse(ajax.response);
}

function newsFeed() {
    const newsFeed = getData(NEWS_URL);
    const newsList = [];
    let template = ` 
        <div>
            <h1>Hacker News</h1>
            <ul>
                {{__news_feed__}}
            </ul>
            <div>
                <a href="#/page/{{__prev_page__}}">ì´ì „ í˜ì´ì§€</a>
                <a href="#/page/{{__next_page__}}">ë‹¤ìŒ í˜ì´ì§€</a>
            </div>
        </div>
    `;
    for(let i = (store.currentPage -1) * 10; i < store.currentPage * 10; i++) {
   newsList.push(`
        <li>
            <a href="#/show/${newsFeed[i].id}">
                ${newsFeed[i].title} - (${newsFeed[i].comments_count});
            </a>
        </li>
   `);
}
    template = template.replace('{{__news_feed__}}', newsList.join(''));
    template = template.replace('{{__prev_page__}}', store.currentPage > 1 ? store.currentPage -1 : 1);;
    template = template.replace('{{__next_page__}}', store.currentPage + 1);
    container.innerHTML = template;
};

function newsDetail() {

        //location - ë¸Œë¼ìš°ì € ê¸°ë³¸ ì œê³µ ê°ì²´. í•´ì‹œë¼ëŠ” ê°ì²´ë¥¼ ì‰½ê²Œ ì°¾ì„ ìˆ˜ ìˆìŒ
        //ì½˜ì†”ë¡œ location.hashí•˜ë©´ #id ê°’ì´ ë‚˜íƒ€ë‚˜ëŠ”ë° #ì€ í•„ìš” ì—†ìœ¼ë¯€ë¡œ substr()ì„ ì´ìš©í•´ì„œ í•„ìš”ì—†ëŠ” ê°’ì„ ìë¥´ê³  ì´ìš¤
        // substr() 0ë¶€í„° ì‹œì‘. ì…ë ¥í•œ ìˆœì„œë¶€í„° ë’¤ì˜ ë‚´ìš©ì„ ë„ì–´ì¤Œ. 1ì…ë ¥ì‹œ 0ê°’ ì˜ë¼ë²„ë¦¼
        const id = location.hash.substr(7);
        console.log(location.hash);
        //replace()ë¥¼ ì´ìš©í•´ CONTENT_URLì˜ @id ê°’ì„ const id ê°’ìœ¼ë¡œ ë³€ê²½
        const newsContent = getData(CONTENT_URL.replace('@id',id));
        container.innerHTML = `
            <h1>${newsContent.title}</h1>
            <div>
                <a href='#/page/${store.currentPage}'>ëª©ë¡ìœ¼ë¡œ</a>
            </div>
        `;
};


function router() {
    const routePath = location.hash;
    if(routePath === '') {
        newsFeed();
        //routePathì— #/page/ë¼ëŠ” ê°’ì´ ìˆìœ¼ë©´ 0 ì´ìƒì˜ ìœ„ì¹˜ê°’ ë¦¬í„´. ì—†ìœ¼ë©´ -1 ë¦¬í„´ ì¦‰ ê°’ì´ ìˆìœ¼ë©´ hashchangeê°€ ë°œìƒ
    } else if(routePath.indexOf('#/page/') >= 0 ) {
        store.currentPage = Number(routePath.substr(7));
        newsFeed();
    } else {
        newsDetail();
    }
}
//#(hash)ê°’ì— ë”°ë¼ ì›í•˜ëŠ” ëª©ë¡ì„ ë³´ì—¬ì¤˜ì•¼ í•˜ë¯€ë¡œ router í˜¸ì¶œ 
window.addEventListener('hashchange', router);

router();